.header {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.header__main_line {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__main_line .caption {
  display: flex;
  align-items: center;
  font-size: 22px;
  line-height: 34px;
  color: #626262;
  white-space: nowrap;
}
.header__main_line .caption .date_start,
.header__main_line .caption .date_end {
  margin: 0 5px;
  font-weight: bold;
  font-size: 22px;
  line-height: 34px;
  color: #626262;
}
.header__main_line .links_block {
  display: flex;
  align-items: center;
}
.header__main_line .links_block .dialogs {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 11px;
  line-height: normal;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #626262;
  padding: 10px 0;
  white-space: nowrap;
}
.header__main_line .links_block .dialogs .icon_plain {
  margin-right: 10px;
}
.header__main_line .links_block .dots_menu {
  position: relative;
}
.header__main_line .links_block .dots_menu .icon_dots {
  padding: 15px;
  cursor: pointer;
}
.header__main_line .links_block .dots_menu .menu {
  position: absolute;
  top: 40px;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 172px;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.12);
  color: #838383;
}
.header__main_line .links_block .dots_menu .menu__item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 12px 16px;
  background: #ffffff;
  transition: all 0.3s;
  color: #838383;
  text-decoration: none;
}
.header__main_line .links_block .dots_menu .menu__item:hover {
  transition: all 0.3s;
  background: #f7f7f7;
}
.header__main_line .links_block .dots_menu .menu__item:first-child {
  border-radius: 5px 5px 0 0;
}
.header__main_line .links_block .dots_menu .menu__item:last-child {
  border-radius: 0 0 5px 5px;
}
.header__main_line .links_block .dots_menu .menu__item .icon {
  margin-right: 15px;
}
.header .banner_block {
  display: flex;
}
.header .banner_block .banner.self_check {
  font-weight: bold;
  font-size: 12px;
  line-height: 18px;
  color: #949494;
  background: #f2f2f2;
  border: 1px solid #949494;
  box-sizing: border-box;
  border-radius: 25px;
  padding: 4px 11px;
}
.header .banner_block .banner.UK_check {
  font-weight: bold;
  font-size: 12px;
  line-height: 18px;
  color: #626262;
  background: #fede48;
  border-radius: 25px;
  padding: 4px 11px;
}
