body {
  font-family: Arial, Helvetica, sans-serif;
  min-width: 1280px;
  background: #f2f2f2;
  margin: 8px 20px 40px;
  position: relative;
}
body:after {
  content: "";
  display: block;
  position: absolute;
  background: url('./img/folder.png') no-repeat;
  right: 90px;
  top: 60px;
  width: 320px;
  height: 385px;
  z-index: -1;
}
.scrollbar {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 9px;
  display: block;
}
.scrollbar .scroll {
  height: 100px;
  width: 9px;
  background: #a99f9f;
  border-radius: 50px;
  position: relative;
  right: 0;
  cursor: pointer;
}
a {
  text-decoration: none;
}
.accounting_ca {
  position: relative;
}
.accounting_ca__main_info {
  overflow: hidden;
}
.accounting_ca .light_wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
.accounting_ca .light_wrap .table_critical_bugs {
  display: flex;
  flex-direction: column;
  z-index: 1001;
  width: 90%;
  min-width: 1150px;
}
.accounting_ca .light_wrap .table_critical_bugs__header {
  background: #ef5f50;
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 16px;
  color: #ffffff;
  padding: 11px 7px 11px 23px;
}
.accounting_ca .light_wrap .table_critical_bugs__header .right_block {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.accounting_ca .light_wrap .table_critical_bugs__header .right_block .cross {
  cursor: pointer;
  width: 14px;
  height: 14px;
  padding: 0px 15px;
}
.accounting_ca .light_wrap .table_critical_bugs .scroll-container {
  overflow: auto;
  max-height: 600px;
}
.accounting_ca .light_wrap .table_critical_bugs__body {
  background: #ffffff;
}
.accounting_ca .light_wrap .table_critical_bugs__body .first_field,
.accounting_ca .light_wrap .table_critical_bugs__body .field {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  line-height: 25px;
  color: #979aa4;
  padding: 0 13px 0 23px;
}
.accounting_ca .light_wrap .table_critical_bugs__body .first_field .col_clients,
.accounting_ca .light_wrap .table_critical_bugs__body .field .col_clients,
.accounting_ca .light_wrap .table_critical_bugs__body .first_field .col_period,
.accounting_ca .light_wrap .table_critical_bugs__body .field .col_period {
  flex-shrink: 0;
  padding: 0 5px;
  flex: 0.7;
  width: 100%;
}
.accounting_ca .light_wrap .table_critical_bugs__body .first_field .col_tax,
.accounting_ca .light_wrap .table_critical_bugs__body .field .col_tax {
  flex-shrink: 0;
  padding: 0 5px;
  flex: 0.3;
}
.accounting_ca .light_wrap .table_critical_bugs__body .first_field .col_description,
.accounting_ca .light_wrap .table_critical_bugs__body .field .col_description {
  flex-shrink: 0;
  padding: 0 5px;
  flex: 3.3;
}
.accounting_ca .light_wrap .table_critical_bugs__body .first_field .col_print,
.accounting_ca .light_wrap .table_critical_bugs__body .field .col_print {
  flex-shrink: 0;
  width: 40px;
}
.accounting_ca .light_wrap .table_critical_bugs__body .field {
  height: 68px;
  font-size: 12px;
  line-height: 15px;
  color: #676767;
}
.accounting_ca .light_wrap .table_critical_bugs__body .field:nth-child(even) {
  background: #f9f9f9;
}
.accounting_ca .light_wrap .table_critical_bugs__body .first_field {
  height: 46px;
}
.stub {
  position: fixed;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.stub .stub_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.stub .stub_content .subscription {
  margin: 18px 0;
  font-size: 18px;
  line-height: 27px;
  text-transform: uppercase;
  color: #4d4d4d;
  font-weight: bold;
}
.stub .stub_content .buttons_block {
  display: flex;
  justify-content: space-between;
  width: 380px;
}
.stub .stub_content .buttons_block .go_to_assistantCA {
  cursor: pointer;
  font-size: 15px;
  line-height: 22px;
  color: #a5a5a5;
  background: #f8f8f8;
  border-radius: 5px;
  padding: 6px 15px;
  transition: all 0.3s;
}
.stub .stub_content .buttons_block .go_to_assistantCA:hover {
  transition: all 0.3s;
  color: #414141;
}
.stub .stub_content .buttons_block .run_check {
  font-size: 15px;
  line-height: 22px;
  color: #3e3e3e;
  background: #fedd46;
  border-radius: 5px;
  padding: 6px 15px;
  transition: all 0.3s;
}
.stub .stub_content .buttons_block .run_check:hover {
  transition: all 0.3s;
  background: #d3b83f;
}
