.graphics_area {
  display: flex;
  justify-content: space-between;
  min-width: 1452px;
}
.graphics_area .left_block,
.graphics_area .right_block {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 749px;
  flex: 1;
  height: 480px;
  background: #ffffff;
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  padding-bottom: 87px;
}
.graphics_area .left_block .head_text,
.graphics_area .right_block .head_text {
  font-weight: bold;
  font-size: 13px;
  line-height: normal;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #676767;
  margin: 0;
  padding: 17px 30px;
  align-self: flex-start;
}
.graphics_area .left_block #contribution,
.graphics_area .right_block #contribution,
.graphics_area .left_block #revenue,
.graphics_area .right_block #revenue {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 600px;
  height: 595px;
}
.graphics_area .left_block #contribution .amcharts-chart-div,
.graphics_area .right_block #contribution .amcharts-chart-div,
.graphics_area .left_block #revenue .amcharts-chart-div,
.graphics_area .right_block #revenue .amcharts-chart-div {
  height: 320px !important;
}
.graphics_area .left_block #contribution .amcharts-legend-div,
.graphics_area .right_block #contribution .amcharts-legend-div,
.graphics_area .left_block #revenue .amcharts-legend-div,
.graphics_area .right_block #revenue .amcharts-legend-div {
  height: 177px !important;
}
.graphics_area .left_block {
  margin-right: 15px;
}
